___CSS_LOADER_EXPORT___.push([module.id, `:root{
  --grid-size: 75px;
  --row-and-width: 8;
  --ship-section-size: 73px;
}
  
* {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  user-select: none;
  -webkit-user-drag: none;
}

.menu{
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-bottom: 4rem;
}

.menu button, .lock-button{
  padding: 0.5rem 1rem;
  font-size: 1.5rem;
  margin-left: 2rem;
}

.menu button:hover, .lock-button{
  cursor: pointer;
}

.menu h1{
  font-size: 8rem;
}

.menu h2{
  font-size: 4rem;
}

.player-container{
  margin: 2rem;
  display: flex;
  position: relative;
}


.cell-container {
  width: fit-content;
  border: 1px solid black;
  display: grid;
  grid-template-rows: repeat(var(--row-and-width), var(--grid-size));
  grid-template-columns: repeat(var(--row-and-width), var(--grid-size));
  z-index: 2;
}

.cell-container-atk-mode{
  margin-left: 5rem;
}

.cell {
  border: 1px solid black;
  text-align: center;
  font-size: 2.5rem;
}

.ship-container{
  width: calc(var(--ship-section-size) * 5);
  background-color: aqua;
  z-index: 0;
}

.ship {
  position: absolute;
  border: 1px solid black;
  display: flex;
  z-index: 1;
}

.ship-section {
  width:  var(--ship-section-size);
  height: var(--ship-section-size);
  border: 1px solid blue;
  background-color: greenyellow;
}

.ship-section:hover {
  cursor: grab;
}

.hidden{
  opacity: 1;
}

main ul, #guide{
  font-size: 1.2rem;
  margin-left: 3rem;
}

#guide{

  margin-left: 2rem;
  font-size: 1.2rem;
  font-weight: bold;
}

